<template>
  <div>
    <div style="margin-bottom: 10px">
      <el-button type="primary" @click="addData">添加数据</el-button>
    </div>
    <el-table stripe border :data="data" v-if="data.length > 0">
      <!-- <el-table-column
        :key="key"
        v-for="(item, key) in formData"
        :prop="key"
        :label="item.txt"
      ></el-table-column> -->
      <el-table-column
        :key="item"
        v-for="item in columnData"
        :prop="item"
        :label="item"
      ></el-table-column>
      <el-table-column label="操作">
        <template slot-scope="scope">
          <el-button size="mini" type="danger" @click="delData(scope.row)"
            >删除</el-button
          >
        </template>
      </el-table-column>
    </el-table>
    <!-- 添加数据的表格 -->
    <el-dialog :title="label" :visible.sync="isShowDiaLog">
      <el-form :modal="formItem">
        <el-form-item :label="key" v-for="(value, key) in formItem" :key="key">
          <!-- v-if="formData[key].type == 'string' || formData[key].type == 'string'" -->
        
            <!-- 选择路由表中的配置 -->
            <template v-if="isTableId(key)"> 
              <el-select :value="formItem[key]" placeholder="请选择路由id" @change="onInput($event, key)">
                <el-option
                  v-for="item in routeConfigData"
                  :key="item.id"
                  :label="item.id"
                  :value="item.id">
                </el-option>
              </el-select>
            </template>


            <template v-if="formData[key].type && formData[key].type == 'select'"> 
              <el-select :value="formItem[key]" placeholder="请选择源接口类型" @change="onInput($event, key)">
                <el-option
                  v-for="(subItem, subIndex) in ['access', 'core', 'cp', 'n9core']"
                  :key="subIndex"
                  :label="subItem"
                  :value="subItem">
                </el-option>
              </el-select>
            </template>

            <template v-else>
              <el-input
                :value="formItem[key]"
                @input="onInput($event, key)"
              ></el-input>
            </template>

          <!-- <template v-else>
            
          </template> -->
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="handleCancel">取 消</el-button>
        <el-button type="primary" @click="handleConfirm"
          >确 定</el-button
        >
      </div>
    </el-dialog>
  </div>
</template>

<script>
import { fetchSetConfig } from "@/api/demo/index.js";
 import EventBus from './eventbus';
export default {
  data() {
    return {
      columnData: {}, //表格显示的数据，因为表格显示的和传的不一样，所以不能共用，只能等到有数据才会显示
      isShowDiaLog: false, //显示添加数据的弹窗
      formItem: {}, //添加的数据
      initFormItem: {} //默认添加的数据，再添加完后需要置为初始化的
    };
  },
  watch: {
    data: {
      handler(val) {
        if (
          Array.isArray(val) &&
          val.length &&
          val[0] instanceof Object &&
          !Object.keys(this.columnData).length
        ) {
          this.columnData = Object.keys(val[0]);
        }
      },
      immediate: true,
    },
    routeConfigData: {
      handler(val) {
        console.log('此时的routeConfigData', val)
      }
    },
    formData: {
      handler(val) {
        let data = Object.keys(val); //配置项的数据
        if (val && data.length) {
          data.map((item) => {
/*             
            if((val[item].type && val[item].type === 'Array')) { //应用规则那里rules是个数组
              console.log('item', this.isObject(val[item]))
              let subData = Object.keys(this.formItem[item].sub);
              console.log('===>hebe', subData);
              this.formItem[item] = {}
              subData.map((subItem) => {
                this.formItem[item][subItem] = '';
              })
              console.log('===>item', this.formItem);
            } else { */
              this.formItem[item] = "";
            // }
          });
          this.initFormItem = {...this.formItem}
        }
      },
      immediate: true,
    },
  },
  props: {
    type: { //tab
      default: "",
      type: String,
    },
    //添加的数据表单
    formData: {
      type: Object,
      default() {
        return {};
      },
    },
    //instanceValue
    instanceValue: {
      default: "",
      type: String,
    },
    //sequenceNo
    sequenceNo: {
      type: Number,
      default: 1000,
    },
    //中文tab
    label: {
      default: "",
      type: String,
    },
    //表格显示的数据
    data: {
      type: Array,
      default() {
        return [];
      },
    },
    //路由表
    routeConfigData: {
      type: Array,
      default() {
        return [];
      },
    }
  },
  created() {},
  mounted(){},
  methods: {
    isObject(data) {
      return data instanceof Object
    },
    //判断是不是路由表，就需要可选数据
    isTableId(id){
      let type = ['net interface', 'nwi', 'pfcp endpoint']; //只有这几个tab下需要路由表里的数据
      let typeData = ['table_id_v4', 'table_id_v6', 'table_id' ]
      if(type.indexOf(this.type) > -1 && typeData.indexOf(id) > -1) {
        return true
      } 
      return false
    },

    //删除数据
    delData(row) {
      let params = {
        request_type: this.type +' del',
        instance_id: this.instanceValue,
        sequence_no: 1000,
        data: [row]
      }
      fetchSetConfig(params).then((res) => {
        if (res.status === true) {
          this.$message.success("删除成功");
          EventBus.$emit('fetchGetConfig', this.type);
        } else {
          this.$message.error(res.error || "网络错误，请稍后再试");
        }
      });
    },
    //增加数据
    addData() {
      if (this.instanceValue == "") {
        this.$message.warning("请先选择实例");
        return;
      }
      this.isShowDiaLog = true;
    },
    //确定按钮
    handleConfirm(){
      for(let key in this.formItem) {
        console.log('key', key)
        if(this.formItem[key] == '') {
          this.$message.error("请填写完数据再提交");
          return
        }
        //判断数字类型
        if(this.formData[key].type == 'int') {
          let data = Number(this.formItem[key])
          if(isNaN(data)) {
            this.$message.error(`${key}请填写int类型提交`);
            return
          }
          this.formItem[key] = data
        }
      }
      let params = {
        request_type: this.type +' add',
        instance_id: this.instanceValue,
        sequence_no: this.sequenceNo,
        data: [this.formItem]
      }
      fetchSetConfig(params).then((res) => {
        if (res.status === true) {
          this.$message.success("添加成功");
          this.formItem = {...this.initFormItem}
          this.isShowDiaLog = false
          EventBus.$emit('fetchGetConfig', this.type);
        } else {
          this.$message.error(res.error || "网络错误，请稍后再试");
        }
      });
    },
    //取消按钮
    handleCancel(){
      this.formItem = {...this.initFormItem}
      this.isShowDiaLog = false;
    },
    //数据监听，形成双向绑定，这里不能使用v-model
    onInput(value, name){
      this.formItem[name] = value
      this.formItem = Object.assign({},this.formItem)
    }
  },
};
</script>

<style></style>
