<template>
  <d2-container>
    <template slot="header">
      <el-select
        v-model="instanceValue"
        placeholder="请选择您的设备"
        @change="getUpfManage"
      >
        <el-option
          v-for="item in options"
          :key="item.instanceId"
          :label="item.instanceId.split('#')[0]"
          :value="item.instanceId"
        >
        </el-option>
      </el-select>
    </template>
    <el-tabs
      type="border-card"
      v-model="tab"
      @tab-click="clickTab"
      v-loading="tabLoading"
      element-loading-text="正在向服务器请求数据"
      element-loading-spinner="el-icon-loading"
      element-loading-background="rgba(0, 0, 0, 0.8)"
    >
      <el-tab-pane label="CPE管理" name="cpe configuration">
        <el-alert
          v-if="this.instanceValue == ''"
          title="请先选择实例"
          type="warning"
        >
        </el-alert>
        <el-form ref="form" :model="cpe" label-width="220px">
          <template v-for="(value, key) in cpe">
            <el-form-item :label="key" :key="key">
              <el-input
                v-if="
                  cpeType[key].type &&
                  (cpeType[key].type === 'string' ||
                    cpeType[key].type === 'int')
                "
                v-model="cpe[key]"
                class="input-data"
              ></el-input>
            </el-form-item>
          </template>
          <el-form-item>
            <el-button type="primary" @click="onSubmitCpeDpe"
              >添加/修改</el-button
            >
          </el-form-item>
        </el-form>
      </el-tab-pane>

      <el-tab-pane label="DPE管理" name="dpe configuration" :lazy="true">
        <el-alert
          v-if="this.instanceValue == ''"
          title="请先选择实例"
          type="warning"
        ></el-alert>
        <el-form ref="form1" :model="dpe" label-width="220px">
          <template v-for="(value, key) in dpe">
            <el-form-item :label="key" :key="key">
              <el-input
                v-if="
                  dpeType[key].type === 'string' || dpeType[key].type === 'int'
                "
                v-model="dpe[key]"
                class="input-data"
              ></el-input>
              <el-switch
                v-if="dpeType[key].type === 'bool'"
                v-model="dpe[key]"
              ></el-switch>
            </el-form-item>
          </template>
          <el-form-item>
            <el-button type="primary" @click="onSubmitCpeDpe"
              >添加/修改</el-button
            >
          </el-form-item>
        </el-form>
      </el-tab-pane>

      <el-tab-pane
        label="CPE N4 Listen Address配置"
        name="cpe n4 listen address"
      >
        <el-form ref="form_dpe_n4" :model="cpeN4" label-width="220px">
          <template v-for="(value, key) in cpeN4">
            <el-form-item :label="key" :key="key">
              <el-input
                v-if="cpeN4Type[key].type === 'string'"
                v-model="cpeN4[key]"
                class="input-data"
              ></el-input>
            </el-form-item>
          </template>
          <el-form-item>
            <el-button type="primary" @click="onSubmitCpeDpe"
              >添加/修改</el-button
            >
          </el-form-item>
        </el-form>
      </el-tab-pane>

      <el-tab-pane label="路由表项配置" :lazy="true" name="route table">
        <routeConfig
          :data="routeData"
          label="路由表项配置"
          type="route table"
          :formData="route_table"
          :instanceValue="instanceValue"
        ></routeConfig>
      </el-tab-pane>
      <el-tab-pane label="网络接口配置" :lazy="true" name="net interface">
        <routeConfig
          label="网络接口配置"
          :data="netInterfaceData"
          type="net interface"
          :formData="net_interface"
          :instanceValue="instanceValue"
          :routeConfigData="routeData"
        ></routeConfig>
      </el-tab-pane>
      <el-tab-pane label="数据接口配置" :lazy="true" name="nwi">
        <routeConfig
          label="数据接口配置"
          :data="nwiData"
          type="nwi"
          :formData="nwi"
          :instanceValue="instanceValue"
          :routeConfigData="routeData"
        ></routeConfig>
      </el-tab-pane>
      <el-tab-pane label="网络实例配置" :lazy="true" name="network instance">
        <routeConfig
          :data="networkInstanceData"
          type="network instance"
          :instanceValue="instanceValue"
          :formData="network_instance"
        ></routeConfig>
      </el-tab-pane>
      <el-tab-pane
        label="默认网络实例配置"
        :lazy="true"
        name="default network instance"
      >
        <routeConfig
          :data="dNetworkInstanceData"
          type="default network instance"
          :formData="default_network_instance"
          :instanceValue="instanceValue"
        ></routeConfig>
      </el-tab-pane>
      <el-tab-pane label="PFCP端点配置" :lazy="true" name="pfcp endpoint">
        <routeConfig
          :data="pfcpendpointData"
          type="pfcp endpoint"
          :instanceValue="instanceValue"
          :formData="pfcp_endpoint"
          :routeConfigData="routeData"
        ></routeConfig>
      </el-tab-pane>
      <el-tab-pane label="GTPU端点配置" :lazy="true" name="gtpu endpoint">
        <routeConfig
          :instanceValue="instanceValue"
          :data="gtpuEndpointData"
          type="gtpu endpoint"
          :formData="gtpu_endpoint"
        ></routeConfig>
      </el-tab-pane>
      <el-tab-pane label="应用规则配置" :lazy="true" name="applications">
        <routeConfig
          :instanceValue="instanceValue"
          :data="applicationsData"
          type="applications"
          :formData="applications"
        ></routeConfig>
      </el-tab-pane>
      <el-tab-pane label="IP地址池配置" :lazy="true" name="ip pool">
        <routeConfig
          :instanceValue="instanceValue"
          :data="ipPoolData"
          type="ip pool"
          :formData="ipPool_data"
        ></routeConfig>
      </el-tab-pane>
      <el-tab-pane label="流超时配置" :lazy="true" name="flow timeout">
        <el-form
          ref="form_flow_timeout"
          :model="flowTimeout"
          label-width="220px"
        >
          <template v-for="(value, key) in flowTimeout">
            <el-form-item :label="key" :key="key">
              <el-input
                v-if="flowTimeoutType[key].type === 'int'"
                v-model="flowTimeout[key]"
                class="input-data"
              ></el-input>
            </el-form-item>
          </template>
          <el-form-item>
            <el-button type="primary" @click="onSubmitFlowTimeout"
              >添加/修改</el-button
            >
          </el-form-item>
        </el-form>
      </el-tab-pane>
    </el-tabs>
  </d2-container>
</template>

<script>
import mixinGetInstanceListMixin from "./mixinGetInstanceList";
import { fetchGetConfig, fetchSetConfig } from "@/api/demo/index.js";
import { Message } from "element-ui";
import EventBus from "./eventbus";
import {
  cpeType,
  cpeN4Type,
  dpeType,
  flowTimeoutType,
  route_config,
  network_interface,
  nwi_config,
  network_instance,
  default_network_instance,
  pfcp_endpoint,
  gtpu_endpoint,
  applications,
  ipPool_data,
} from "./config.js";
import routeConfig from "./routeConfig";
export default {
  mixins: [mixinGetInstanceListMixin],
  name: "upf",
  components: {
    routeConfig,
  },
  data() {
    return {
      options: [], // upf下面的实例配置
      instanceValue: "", // 当前选中的实例
      tab: "cpe configuration", // 当前的tab
      tabLoading: false,
      cpeType: cpeType, //cpe属性
      cpeN4Type: cpeN4Type, //cpe n4属性
      dpeType: dpeType, //dpe属性
      flowTimeoutType: flowTimeoutType, //flowTimeoutType 属性
      cpe: {
        max_session: "",
        max_incoming_req: "",
        max_outcoming_req: "",
        heartbeat_interval: "",
        inactivity_timer: "",
        n1_request: "",
        t1_response_timeout: "",
      },
      cpeN4: {
        pfcp_local_ipv4_address: "",
        pfcp_local_ipv6_address: "",
      },
      dpe: {
        dpe_number: "",
        dpe_max_session: "",
        dpe_flow_factor: "",
        dp_accelerate: false,
        dpe_flow_count_per_session: "",
        dpe_flow_idle_time_per_session: "",
        n3_interface_ipv4_address: "",
        n6_interface_ipv4_address: "",
        n9_interface_ipv4_address: "",
        n3_interface_ipv6_address: "",
        n6_interface_ipv6_address: "",
        n9_interface_ipv6_address: "",
      },
      flowTimeout: {},

      //关于数据的获取，全是数组格式的通用表格组件,添加数据的时候通用数据组件
      routeData: [], //路由表配置数据
      netInterfaceData: [], //网络接口配置数据
      nwiData: [], //数据接口配置数据
      networkInstanceData: [], //网络实例接口配置数据
      dNetworkInstanceData: [], //默认实例接口配置数据
      pfcpendpointData: [], //PFCP端点配置
      gtpuEndpointData: [], //dtpu端点配置
      applicationsData: [], //应用端点配置
      ipPoolData: [], //IP地址池配置

      route_table: route_config,
      net_interface: network_interface,
      nwi: nwi_config,
      network_instance,
      default_network_instance,
      pfcp_endpoint,
      gtpu_endpoint,
      applications,
      ipPool_data,
    };
  },
  created() {
    this.getUpfServiceInstance();
    let that = this;
    EventBus.$on("fetchGetConfig", function (type) {
      console.log("eventbusgettype", type);
      that.getUpfManage();
    });
  },
  methods: {
    // 获取当前upf下面的实例
    getUpfServiceInstance() {
      this.getServiceInstance("upf")
        .then((res) => {
          this.options = res.hosts;
          if (this.options.length > 0) {
            this.instanceValue = this.options[0].instanceId;
            this.getUpfManage();
            //默认还需要获取router的内容，其他tab有用到
            this.getUpfManage("route table");
          }
        })
        .catch((e) => {
          this.$alert(e, "网络请求发生错误", {
            confirmButtonText: "确定",
            callback: (action) => {},
          });
        });
    },
    // 获取当前的DPE和CPE管理以及其他配置的内容
    getUpfManage(type) {
      if (this.instanceValue == "") {
        this.$message.warning("请先选择实例");
        return;
      }
      this.tabLoading = true;
      console.log("当前选中的tab,", this.tab);
      console.log("routerConfi", this.routeData);
      let data = {
        request_type: (type || this.tab) + " get",
        instance_id: this.instanceValue,
        sequence_no: 1000,
      };
      //请求实例列表
      fetchGetConfig(data)
        .then((res) => {
          console.log("此时请求列表res", res);
          this.tabLoading = false;
          if (res.status === true) {
            this.dealConfig(res.data);
            if (type) {
              this.dealConfig(res.data, type);
            }
          } else {
            this.$message.error(res.error || "网络错误，请稍后再试" + res);
          }
        })
        .catch((e) => {
          this.tabLoading = false;
          console.log("error:", e);
          this.$alert(e, "网络请求发生错误", {
            confirmButtonText: "确定",
            callback: (action) => {},
          });
        });
    },
    /**
     * 处理配置的数据
     */
    dealConfig(data, type) {
      let switchData = type || this.tab;
      switch (switchData) {
        case "cpe configuration":
          this.cpe = data[0];
          break;
        case "dpe configuration":
          this.dpe = data[0];
          break;
        case "route table":
          this.routeData = data;
          console.log("===>type", type);
          break;
        case "net interface":
          this.netInterfaceData = data;
          break;
        case "nwi":
          this.nwiData = data;
          break;
        case "network instance":
          this.networkInstanceData = data;
          break;
        case "default network instance":
          this.dNetworkInstanceData = data;
          break;
        case "pfcp endpoint":
          this.pfcpendpointData = data;
          break;
        case "gtpu endpoint":
          this.gtpuEndpointData = data;
          break;
        case "applications":
          this.applicationsData = data;
          break;
        case "ip pool":
          this.ipPoolData = data;
          break;
        case "flow timeout":
          this.flowTimeout = data;
          console.log(data);
          break;
        case "cpe n4 listen address":
          this.cpeN4 = data[0];
          console.log(data);
          break;
      }
    },
    /**
     * 切换tab
     * tab: cpe, dpe
     */
    clickTab(tab) {
      console.log("tab--", tab);
      console.log("切换的tab", this.tab);
      this.getUpfManage();
    },
    /**
     * cpe和dpe保存
     */
    onSubmitCpeDpe() {
      console.log("cpe", this.tab);
      let setData = "";
      if (this.tab === "cpe configuration") {
        for (let i in this.cpe) {
          if (this.cpe[i] === "") {
            Message({
              message: "请填入" + this.cpe[i],
              type: "error",
            });
            return;
          }
          if (Number(this.cpe[i]) === "NaN") {
            Message({
              message: this.cpe[i] + "请输入数字类型",
              type: "error",
            });
            return;
          }
          this.cpe[i] = Number(this.cpe[i]);
        }
        setData = this.cpe;
      } else if (this.tab === "cpe n4 listen address") {
        for (let i in this.cpeN4) {
          if (this.cpeN4[i] === "") {
            Message({
              message: "请填入" + this.cpeN4[i],
              type: "error",
            });
            return;
          }
        }
        setData = this.cpeN4;
      } else {
        for (let i in this.dpe) {
          if (
            (this.dpeType[i].type === "int" ||
              this.dpeType[i].type === "string") &&
            this.dpe[i] === ""
          ) {
            Message({
              message: "请填入" + this.dpe[i],
              type: "error",
            });
            return;
          }
          if (this.dpeType[i].type === "int" && Number(this.dpe[i]) === "NaN") {
            Message({
              message: this.dpe[i] + "请输入数字类型",
              type: "error",
            });
            return;
          }
          if (this.dpeType[i].type === "int" && Number(this.dpe[i]) !== "NaN") {
            this.dpe[i] = Number(this.dpe[i]);
          }
        }
        setData = this.dpe;
      }
      let params = {
        request_type: this.tab + " set",
        instance_id: this.instanceValue,
        sequence_no: 1000,
        data: [setData],
      };
      fetchSetConfig(params).then((res) => {
        if (res.status === true) {
          this.$message.success("设置成功");
          console.log("获取的dpe和cpe", this[this.tab]);
        } else {
          this.$message.error(res.error || "网络错误，请稍后再试");
        }
      });
    },
    /**
     * 流超时配置保存
     */
    onSubmitFlowTimeout() {
      for (let i in this.flowTimeout) {
        if (this.cpe[i] === "") {
          Message({
            message: "请填入" + this.cpe[i],
            type: "error",
          });
          return;
        }
        if (Number(this.cpe[i]) === "NaN") {
          Message({
            message: this.cpe[i] + "请输入数字类型",
            type: "error",
          });
          return;
        }
        this.cpe[i] = Number(this.cpe[i]);
      }

      let params = {
        request_type: this.tab + " set",
        instance_id: this.instanceValue,
        sequence_no: 1000,
        data: this.flowTimeout,
      };
      fetchSetConfig(params).then((res) => {
        if (res.status === true) {
          this.$message.success("设置成功");
        } else {
          this.$message.error(res.error || "网络错误，请稍后再试");
        }
      });
    },
  },
};
</script>
<style  scoped>
.input-data {
  width: 220px;
}
</style>
